import Vue from 'vue'
import Vuex from 'vuex'
import request from '../network/index'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    list: [
      {
        id: 515143066,
        title: 'Time after time ',
        artist: '',
        src: 'http://m8.music.126.net/20210721233736/e62c7e4d66fd17a2cb0d7f28bfa775f7/ymusic/d55d/2230/85d2/70528193634799ac251c4be2fe71d08a.mp3',
        pic: 'https://p1.music.126.net/XbDlX1f7r0cgY42O2yyoaw==/109951163049831563.jpg',
        lrc: '[00:00.000]  : \n[00:01.000]  : \n[00:11.20]\n[00:16.40]\n[00:21.20]\n[00:24.90]\n[00:26.20]\n[00:31.80]\n[00:37.90]\n[00:44.50]Time after time\n[00:46.00]\n[00:49.20]\n[00:54.20]\n[01:00.60]\n[01:21.80]\n[01:27.00]\n[01:31.10]\n[01:36.70]\n[01:41.50]\n[01:46.10]\n[01:54.50]Time after time\n[01:55.90]\n[01:59.90]\n[02:04.60]\n[02:10.40]\n[02:20.40]\n[02:26.60]\n[02:31.80]\n[02:43.30]\n[02:48.50]\n[02:54.60]\n[02:59.60]\n[03:06.00]Time after time\n[03:07.20]\n[03:13.40]\n[03:16.70]\n[03:19.50]\n[03:22.30]\n[03:26.70]\n'
      }
    ]
  },
  mutations: {
    //  
    addMusic(state, item) {
      state.list = state.list.filter((i) => i.id !== item.id)
      state.list.unshift(item)
    },
    // 
    addMusic1(state, item) {
      state.list = state.list.filter((i) => i.id !== item.id)
      state.list.push(item)
    },
    // 
    clear(state) {
      state.list = []
    }
  },
  actions: {
    // 
    async getList(context, payload) {
      // const res = {}
      // console.log(payload)
      // payload={id,insert:true} true    false 
      const [{ data: data1 }, { data: data2 }, { data: data3 }] = await Promise.all([
        context.dispatch('getSrc', payload.id),
        context.dispatch('getInfo', payload.id),
        context.dispatch('getLyric', payload.id)
      ])
      console.log(data3)
      const lrc = data3.nolyric || data3.uncollected ? '[00:00.000]' : data3.lrc.lyric
      const res = {
        id: payload.id,
        title: data2.songs[0].name,
        artist: data2.songs[0].ar[0].name,
        src: data1.data[0].url,
        pic: data2.songs[0].al.picUrl,
        lrc
      }
      console.log(res)
      if (payload.insert) {
        context.commit('addMusic', res)
      } else {
        context.commit('addMusic1', res)
      }
    },
    // src  /song/url?id=33894312
    getSrc(context, id) {
      return new Promise((resolve, reject) => {
        request(`/song/url?id=${id}`).then(res => resolve(res), err => reject(err))
      })
    },
    //  title pic artist /song/detail?ids=347230
    getInfo(context, id) {
      return new Promise((resolve, reject) => {
        request(`/song/detail?ids=${id}`).then(res => resolve(res), err => reject(err))
      })
    },
    // 
    getLyric(context, id) {
      return new Promise((resolve, reject) => {
        request(`/lyric?id=${id}`).then(res => resolve(res), err => reject(err))
      })
    },
  },
  modules: {

  }
})
